import { MetaApiData, SessionStorageKey } from '@app/storage/session/shared';
import { useEffect, useState } from 'react';

export const useContentMetaApiData = (): MetaApiData => {
  const [metaApiData, setMetaApiData] = useState<MetaApiData>(
    {} as MetaApiData,
  );

  useEffect(() => {
    chrome.storage.session.get('metaApiData', (result) => {
      if (result.metaApiData) {
        setMetaApiData(result.metaApiData as MetaApiData);
      }
    });

    const listener = (changes: {
      [key: string]: chrome.storage.StorageChange;
    }) => {
      if (changes[SessionStorageKey.MetaApiData]) {
        const newValue = changes[SessionStorageKey.MetaApiData].newValue;
        setMetaApiData(newValue);
      }
    };

    chrome.storage.session.onChanged.addListener(listener);

    return () => {
      chrome.storage.session.onChanged.removeListener(listener);
    };
  }, []);

  return metaApiData;
};
