import { TelegramIcon } from '../../../../TelegramIcon';
import { CloseIcon } from '../../../assets/icons/CloseIcon';
import { useBanner } from '../../../hooks/useBanner';

export function TelegramBanner(): JSX.Element | null {
  const { isShown, handleCloseClick } = useBanner();

  if (!isShown) {
    return null;
  }

  return (
    <div className="ad-premium-banner">
      <button
        type="button"
        className="banner-close-button"
        onClick={handleCloseClick}
      >
        <CloseIcon />
      </button>
      <div className="tg-banner">
        <div className="tg-banner-info">
          <div className="banner-text">
            <span className="title">
                 Telegram- 
            </span>
            ,         
               .
          </div>
          <a
            className="tg-link"
            href={`https://t.me/${process.env.TELEGRAM_URL}`}
            target="_blank"
            rel="noreferrer"
          >
              Telegram-
          </a>
        </div>
        <TelegramIcon className="tg-icon" />
      </div>
    </div>
  );
}
