import { useContentMetaApiData } from '@content/hooks/useContentMetaApiData';
import { useEffect, useState } from 'react';

export function useIframeUrl() {
  const [iframeUrl, setIframeUrl] = useState<string | null>(null);
  const { adfoxBannerUrl } = useContentMetaApiData();

  useEffect(() => {
    async function getMetaData() {
      if (!iframeUrl) {
        setIframeUrl(adfoxBannerUrl ?? null);
      }
    }
    void getMetaData();
  }, [iframeUrl, adfoxBannerUrl]);

  return iframeUrl;
}
