import { CloseIcon } from '../../../assets/icons/CloseIcon';
import { HighQuality } from '../assets/icons/HighQuality';
import { Server } from '../assets/icons/Server';
import { Support } from '../assets/icons/Support';
import { VideoPlayer } from '../assets/icons/VideoPlayer';
import { YouboostNeoLogo } from '../assets/icons/YouboostNeoLogo';
import { Grid } from '../assets/bg/Grid';
import { useBanner } from '../../../hooks/useBanner';

const YOUBOOST_FEATURES = [
  {
    title: '   4',
    icon: HighQuality,
  },
  {
    title: ' ',
    icon: Server,
  },
  {
    title: ' ',
    icon: VideoPlayer,
  },
  {
    title: ' ',
    icon: Support,
  },
];

export function YouboostNeoBanner(): JSX.Element | null {
  const { isShown, handleCloseClick } = useBanner();

  if (!isShown) {
    return null;
  }

  return (
    <div className="ad-premium-banner">
      <div className="youboost-neo-banner">
        <button
          type="button"
          className="banner-close-button"
          onClick={handleCloseClick}
        >
          <CloseIcon />
        </button>
        <Grid className="youboost-neo-bg" />
        <div className="youboost-neo-info">
          <YouboostNeoLogo className="youboost-neo-logo" />
          <div className="banner-title">
                 Premium
          </div>
          <a
            className="youboost-neo-link"
            href="https://naruzhu.click/uboostbanner"
            target="_blank"
            rel="noreferrer"
          >
             
          </a>
        </div>
        <div className="youboost-neo-features">
          {YOUBOOST_FEATURES.map((feature) => (
            <div key={feature.title} className="youboost-neo-features-item">
              <feature.icon className="youboost-neo-feature-icon" />
              <span>{feature.title}</span>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
}
