import { CloseIcon } from '../../../../../assets/icons/CloseIcon';
import { useBanner } from '../../../../../hooks/useBanner';
import { useAndroidAppUrl } from '../hooks/useAndroidAppUrl';
import { Logo } from './Logo';

export function YouboostAndroidBanner(): JSX.Element | null {
  const { isShown, handleCloseClick } = useBanner();
  const appUrl = useAndroidAppUrl();

  if (!isShown) {
    return null;
  }

  return (
    <>
      <div className="ad-android-banner">
        <button
          type="button"
          className="banner-close-button"
          onClick={handleCloseClick}
        >
          <CloseIcon />
        </button>
        <div className="android-banner-container">
          <div className="android-banner-content">
            <div className="android-banner-title">
              <p>
                {' '}
                <span className="android-banner-title-main">
                  TV / Android - 
                </span>{' '}
                 
              </p>
            </div>
            <div className="android-banner-description">
              <p className="android-banner-description-block">
                 YouTube   <b>Android-:</b>
                <br /> ,   .
              </p>
            </div>
            <a href={appUrl} className="button-primary-a">
               
            </a>
          </div>
          <div className="android-banner-logo-container">
            <Logo />
          </div>
        </div>
        {/*  */}
      </div>
      <div className="spacer" />
    </>
  );
}
