import { memo, useEffect, useMemo, useRef } from 'react';
import { PremiumActions } from '@app/actions/premium';
import { Feature } from '@app/features/types/Feature';
import { AvailableBanner } from '@app/storage/session/shared';
import { useContentFeaturesList } from '@content/hooks/useContentFeaturesList';
import { useAvailableBanner } from '../hooks/useAvaliableBanner';
import { AdfoxBanner } from './AdfoxBanner/ui/AdfoxBanner';
import { NaruzhuBanner } from './NaruzhuBanner';
import { TelegramBanner } from './TelegramBanner';
import { UltaBanner } from './UltaBanner';
import { YouboostNeoBanner } from './YouboostNeoBanner';
import { YouboostAndroidBanner } from './YouboostPremiumBanner/ui/YouboostAndroidBanner';

const bannersMap: Record<AvailableBanner, () => JSX.Element | null> = {
  [AvailableBanner.Naruzhu]: NaruzhuBanner,
  [AvailableBanner.Telegram]: TelegramBanner,
  [AvailableBanner.Ulta]: UltaBanner,
  [AvailableBanner.YouboostPremium]: YouboostNeoBanner,
  [AvailableBanner.YouboostAndroid]: YouboostAndroidBanner,
};

function YouboostAdsRaw(): JSX.Element | null {
  const isInitialized = useRef(false);

  const { featuresList } = useContentFeaturesList();

  const availableBanners = useAvailableBanner();

  const randomIndex = useMemo(
    () => Math.floor(Math.random() * availableBanners.length),
    [availableBanners],
  );

  const SelectedBanner = useMemo(
    () => bannersMap[availableBanners[randomIndex]],
    [availableBanners, randomIndex],
  );

  const isAdfoxEnabled = useMemo(
    () => featuresList?.[Feature.AdfoxBanner] ?? false,
    [featuresList],
  );

  useEffect(() => {
    if (isInitialized.current) {
      return;
    }

    isInitialized.current = true;

    async function initAdPremiumBanner() {
      await chrome.runtime.sendMessage({
        action: PremiumActions.InitAdPremiumBanner,
      });
    }

    initAdPremiumBanner();
  }, []);

  if (!isAdfoxEnabled && !availableBanners.length) {
    return null;
  }

  if (!isAdfoxEnabled && !SelectedBanner) {
    return null;
  }

  return isAdfoxEnabled ? <AdfoxBanner /> : <SelectedBanner />;
}

export const YouboostAds = memo(YouboostAdsRaw);
