import { useEffect, useState } from 'react';
import {
  LocalStoragePremiumKey,
  getLocalStoragePremiumData,
  setLocalStoragePremiumData,
} from '@app/storage/local/premium';
import { type ClosePremiumAdBannerMessage } from '@app/messages/premium';
import { PremiumActions } from '@app/actions/premium';
import { versionStringToNumber } from '@popup/shared/helpers/versionStringToNumber';
import { useContentMetaApiData } from '@content/hooks/useContentMetaApiData';

export function useBanner(): {
  isShown: boolean;
  handleCloseClick: () => Promise<void>;
} {
  const [isShown, setIsShown] = useState(false);
  const { advMinVersion } = useContentMetaApiData();

  useEffect(() => {
    async function getIsShown(): Promise<void> {
      const { isAdBannerShown } = await getLocalStoragePremiumData(
        LocalStoragePremiumKey.IsAdBannerShown,
      );

      const currentVersion = chrome.runtime.getManifest().version;

      const isShown = Boolean(
        advMinVersion &&
          versionStringToNumber(currentVersion) <=
            versionStringToNumber(advMinVersion) &&
          isAdBannerShown,
      );

      setIsShown(isShown);
    }

    void getIsShown();
  }, [advMinVersion]);

  useEffect(() => {
    function listener(changes: {
      isAdBannerShown?: { newValue: boolean };
    }): void {
      if (changes.isAdBannerShown) {
        setIsShown(changes.isAdBannerShown.newValue);
      }
    }
    chrome.storage.local.onChanged.addListener(listener);
    return () => {
      chrome.storage.local.onChanged.removeListener(listener);
    };
  }, []);

  async function handleCloseClick(): Promise<void> {
    await setLocalStoragePremiumData({
      isAdBannerShown: false,
    });
    await chrome.runtime.sendMessage<ClosePremiumAdBannerMessage>({
      action: PremiumActions.CloseAdPremiumBanner,
    });
  }

  return { isShown, handleCloseClick };
}
