import { CloseIcon } from '../../../assets/icons/CloseIcon';
import { useBanner } from '../../../hooks/useBanner';
import { UltaIcon } from './UltaIcon';

export function UltaBanner(): JSX.Element | null {
  const { isShown, handleCloseClick } = useBanner();

  if (!isShown) {
    return null;
  }

  return (
    <div className="ad-premium-banner">
      <button
        type="button"
        className="banner-close-button"
        onClick={handleCloseClick}
      >
        <CloseIcon />
      </button>
      <div className="ulta-banner">
        <div className="ulta-banner-info">
          <div className="banner-text">
            <span className="title">   Discord </span>
               ,     
            Discord,    .
          </div>
          <a
            className="ulta-link"
            target="_blank"
            href="https://t.me/ultarussia"
            rel="noreferrer"
          >
               Discord
          </a>
        </div>
        <UltaIcon className="ulta-icon" />
      </div>
    </div>
  );
}
