import { useEffect, useState } from 'react';
import { useContentMetaApiData } from '@content/hooks/useContentMetaApiData';

export function useAndroidAppUrl() {
  const [link, setLink] = useState<string>('#');
  const { androidAppUrl } = useContentMetaApiData();

  useEffect(() => {
    async function getIsShown(): Promise<void> {
      setLink(androidAppUrl || '#');
    }
    void getIsShown();
  }, [androidAppUrl]);

  return link;
}
