import React from 'react';
import { createRoot } from 'react-dom/client';
import {
  LocalStorageKey,
  getLocalStorageData,
} from '../../app/storage/local/shared';
import { YouboostAds } from '../components/YouboostAds';

try {
  const root = document.createElement('div');
  root.id = 'youboost-ad-premium-banner-root';

  createRoot(root).render(
    <React.StrictMode>
      <YouboostAds />
    </React.StrictMode>,
  );

  const { currentState } = await getLocalStorageData(
    LocalStorageKey.CurrentState,
  );

  const linkFont = document.createElement('link');
  linkFont.rel = 'stylesheet';
  linkFont.href =
    'https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap';

  document.head.appendChild(linkFont);

  if (currentState === 'connected' && isPageForAdBanner()) {
    const contentsObserver = new MutationObserver((mutations, obs) => {
      const contents = document.getElementById(
        location.pathname === '/watch' ? 'above-the-fold' : 'contents',
      );
      if (contents) {
        renderBanner(root, contents);
        obs.disconnect();
      }
    });
    contentsObserver.observe(document, { childList: true, subtree: true });

    // code for banner injection after navigation on youtube app
    document.addEventListener('yt-page-type-changed', (event) => {
      if (isPageForAdBanner()) {
        const {
          detail: { newPageType, newPageSubtype },
        } = event as CustomEvent<{
          newPageType: string;
          newPageSubtype: string;
        }>;

        const elements = document.querySelectorAll(newPageType);
        let element = elements[0];

        if (elements.length > 1) {
          element =
            Array.from<Element>(elements).find((el) => {
              return el.getAttribute('page-subtype') === newPageSubtype;
            }) ?? elements[0];
        }

        if (element instanceof HTMLElement) {
          const newPageTypeObserver = new MutationObserver((mutations, obs) => {
            const contents = element.querySelector(
              location.pathname === '/watch' ? '#above-the-fold' : '#contents',
            );
            if (contents) {
              renderBanner(root, contents);
              obs.disconnect();
            }
          });
          newPageTypeObserver.observe(document, {
            childList: true,
            subtree: true,
          });
        }
      }
    });
  }
} catch (error) {
  console.error('adPremiumBanner', error);
}

function renderBanner(root: HTMLElement, contents: Element | null): void {
  if (contents instanceof HTMLElement) {
    contents.parentNode?.insertBefore(root, contents);
  }
}

function isPageForAdBanner(): boolean {
  return (
    location.pathname === '/' ||
    location.pathname === '/feed/subscriptions' ||
    location.pathname === '/feed/trending' ||
    location.pathname === '/watch' ||
    location.pathname === '/results'
  );
}
